.content {
  max-width: 1044px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 10px;
}

.wide {
  max-width: max-content;
  margin-left: auto;
  margin-right: auto;
}

#graph {
  height: 100%;
  overflow-x: scroll;
}

.pure-menu-selected {
  background: #eee;
}

.tab {
  background: #eee;
  padding: 1em;
}

.center {
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.alert {
  color: #fff;
  background: orange;
  border-radius: 4px;
  padding: 1em;
}

.file-input {
  width: .1px;
  height: .1px;
  opacity: 0;
  z-index: -1;
  position: absolute;
  overflow: hidden;
}

textarea {
  width: 100%;
  max-width: 100%;
  min-width: 100%;
  min-height: 100px;
}

.button-danger {
  color: #fff;
  background: #d62728;
}

.button-checked {
  color: #fff;
  background-color: #2ca02c;
}

.button-primary, .button-selected {
  color: #fff;
  background-color: #1f77b4;
}

.placeholder {
  min-height: 1em;
}

.form-text {
  max-width: max-content;
  padding: .5em 0;
  display: inline-block;
}

@media screen and (min-width: 48em) {
  .gh-input .load-padder {
    margin-top: 28px;
  }
}

.loading {
  min-height: 100px;
  width: 100%;
  position: relative;
}

.loading:after {
  content: " ";
  width: 64px;
  height: 64px;
  border: 6px solid #000;
  border-color: #000 #0000;
  border-radius: 50%;
  margin: 8px auto;
  animation: lds-dual-ring 1.2s linear infinite;
  display: block;
}

@keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

